}

.form__group {
  transform: translate(0px, 80px);
  opacity: 0;
}
.form__group.select-wrapper {
  position: relative;
  z-index: 2;
}
.form__row {
  margin-bottom: 30px;
}

.input {
  position: relative;
}
.input__field {
  width: 100%;
  font-size: 19px;
  line-height: 23px;
  background: transparent;
  padding: 22px 10px 11px 50px;
  border: 1px solid #FFFFFF;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.3);
  transition: 0.3s ease-in-out;
  border-radius: 5px;
  color: #FFFFFF;
  background: rgba(0, 0, 0, 0.2);
}
.input__field:valid {
  background: rgba(0, 0, 0, 0.4);
}
.input__field:focus {
  border-color: #6CEEC7;
  outline: none;
  background: rgba(0, 0, 0, 0.4);
}
.input__field:focus + .input__placeholder {
  transform: none;
  top: 11px;
  font-size: 11px;
  line-height: 13px;
  color: #6CEEC7;
}
.input__field:hover {
  border-color: #6CEEC7;
}
.input__placeholder {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 65px;
  font-weight: 300;
  font-size: 19px;
  line-height: 23px;
  color: #E0E0E0;
  pointer-events: none;
  user-select: none;
  transition: 0.3s ease-in-out;
}
.input__placeholder.typed {
  transform: none;
  top: 11px;
  font-size: 11px;
  line-height: 13px;
  color: #6CEEC7;
}
.input__icon {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 32px;
  font-size: 1.4rem;
  color: #6CEEC7;
}
.input__error {
  position: absolute;
  right: 15px;
  top: calc(100% + 6px);
  font-weight: 500;
  font-size: 10px;
  line-height: 12px;
  color: #DA5050;
  opacity: 0;
  transition: 0.3s ease-in-out;
  text-align: right;
}
.input__error.visible {
  opacity: 1;
}

.submit-btn {
  background: #6CEEC7;
  font-weight: 500;
  font-size: 19px;
  line-height: 23px;
  color: #000000;
  border: none;
  outline: none;
  border: 1px solid #6CEEC7;
  padding: 17px 50px;
  cursor: pointer;
  border-radius: 5px;
  transition: 0.3s ease-in-out;
}
.submit-btn:hover {
  background: transparent;
  color: #6CEEC7;
}

.select {
  width: 100%;
  height: 58px;
  color: #FFFFFF;
  font-weight: 300;
  font-size: 19px;
  line-height: 23px;
  padding: 22px 10px 11px 50px;
  cursor: pointer;
  position: relative;
  transition: 0.3s ease-in-out;
  text-transform: lowercase;
}
.select:first-letter {
  text-transform: uppercase;
}
.select::after {
  content: "";
  position: absolute;
  width: 24px;
  height: 24px;
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
  transition: 0.3s ease-in-out;
  background: url("../img/svg/select_arrow.svg");
}
.select.active {
  border-color: #6CEEC7;
  background: rgba(0, 0, 0, 0.4);
}
.select.active::after {
  transform: scale(1, -1) translateY(50%);
}
.select.selected {
  background: rgba(0, 0, 0, 0.4);
}
.select__list {
  position: absolute;
  z-index: 10;
  top: calc(100% + 3px);
  left: 15px;
  width: calc(100% - 30px);
  display: none;
  max-height: 159px;
  overflow-y: auto;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  background: #FFFFFF;
  border: 2px solid #FFFFFF;
  padding: 18px 21px;
  cursor: default;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.select__list::-webkit-scrollbar {
  width: 0;
  height: 0;
}
.select__list span {
  display: block;
  text-transform: lowercase;
  font-weight: 300;
  font-size: 15px;
  line-height: 18px;
  color: #5E5E5E;
  padding: 5px 0px;
  margin-bottom: 5px;
  cursor: pointer;
  transition: 0.3s ease-in-out;
  position: relative;
  left: 0;
}
.select__list span:first-letter {
  text-transform: uppercase;
}
.select__list span:hover {
  color: #212121;
  left: 10px;
}
.select__list span.selected {
  color: #212121;
  left: 10px;
}

.checkbox {
  display: flex;
  align-items: center;
}
.checkbox__field {
  display: none;
}
.checkbox__field:checked + label::before {
  background: #6CEEC7 url("../img/svg/check-icon.svg") no-repeat;
  background-position: center center;
  background-size: contain;
}
.checkbox__text {
  position: relative;
  font-size: 15px;
  line-height: 18px;
  padding-left: 31px;
  color: #FFFFFF;
  cursor: pointer;
}
.checkbox__text::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 20px;
  height: 20px;
  border-radius: 3px;
  border: 1px solid #6CEEC7;
  transition: 0.3s ease-in-out;
}
.checkbox__text.error::before {
  border-color: #DA5050;
  background: #DA5050;
}
.checkbox__text:hover::before {
  background: #6CEEC7;
}
.checkbox a {
  color: #6CEEC7;
  padding-bottom: 2px;
  border-bottom: 1px solid #6CEEC7;
  font-size: 15px;
  line-height: 18px;
}

@media screen and (max-width: 575px) {
  .registration-btn {
    max-width: 330px;
    padding: 14px 10px;
    font-size: 16px;
    line-height: 19px;
  }

  .form__row {
    margin-bottom: 0px;
  }

  .form__group.col-6.col-6 {
    flex: 0 0 100%;
    max-width: 100%;
    margin-bottom: 30px;
  }
  .form__group.col-6.email {
    margin-bottom: 40px;
  }
  .form__group.col-6.checkbox {
    margin-bottom: 50px;
  }

  .submit-btn {
    display: block;
    width: 100%;
  }
}
